import React from 'react';

const Precio = (props) => {

    const {precioActual, precio1Semana, precio1Hora, precio1Dia} = props
    const porcentaje1Semana = (precioActual - precio1Semana)*100/precioActual
    const porcentaje1Hora = (precioActual - precio1Hora)*100/precioActual
    const porcentaje1Dia = (precioActual - precio1Dia)*100/precioActual
    return (        
        <div className="card text-white bg-success my-5">
            <div className="card-header">Precio del Bitcoin</div>
            <div className="card-body">
                <h4 className="card-title">Precio Actual: {precioActual.toFixed(2)}</h4>
                <ul className="list-group-flush d-flex justify-content-between align-items-center pt-3">
                    <p className="card-text">Última Hora: {porcentaje1Hora.toFixed(2)}%</p>
                    <p className="card-text">Últimas 24 Horas: {porcentaje1Dia.toFixed(2)}%</p>
                    <p className="card-text">Últimos 7 días: {porcentaje1Semana.toFixed(2)}%</p>
                </ul>                           
            </div>            
        </div>       
    );
};

export default Precio;